<template>
  <div>
    <div class="md" v-html="displayHTML"></div>
  </div>
</template>

<script>
import Prism from 'prismjs'
import marked from 'marked'

export default {
  props: ['content'],

  computed: {
    displayHTML () {
      // retrieve the content we've been passed
      const { content } = this

      // parse the markdown
      const parsed = marked(content, {
        highlight: function (code, lang) {
          // if we don't have a highlighting function for this lanuge, return it as is
          if (!Prism.languages[lang]) {
            return code
          }

          // return the formatted code
          const formatted = Prism.highlight(code, Prism.languages[lang], lang)

          return formatted
        }
      })

      // we need to add the classes to the pre tag in the code blocks, so here's some basic regex to do that
      const correctedClasses = parsed.replace(/<pre><code\sclass="language-(.*?)">/gi, '<pre class="language-$1"><code class="language-$1">')

      return correctedClasses
    }
  }
}
</script>

