<template>
  <li class="article-preview-item">
      <h3>
        <router-link :to="path" active-class="active">{{ article.title }}</router-link>
      </h3>
  </li>
</template>

<script>
export default {
  props: ['article', 'index'],

  computed: {
    path () {
      if (this.article.slug) {
        return `/article/${this.article.slug}`
      }
      return `/article/${this.index}`
    }
  }
}
</script>

