<template>
  <div>
    <article-preview-aside />
    <div class="content">
      <p>
        <router-link to="/" class="back"><font-awesome-icon class="icon" :icon="['fas', 'angle-left']" /> Back to Portfolio List</router-link>
      </p>
      <h1>{{ currentArticle.title }}</h1>
      <h2>{{ currentArticle.subtitle }}</h2>
      <ul class="tags">
        <li v-for="(tag, tagIndex) in currentArticle.tags" :key="tagIndex">
          <tag-pill :text="tag" />
        </li>
      </ul>
      <markdown-display :content="currentArticle.body"></markdown-display>
    </div>
  </div>
</template>

<script>
import MarkdownDisplay from '@/components/MarkdownDisplay.vue'
import TagPill from '@/components/TagPill.vue'
import ArticlePreviewAside from '@/components/ArticlePreviewAside.vue'
import { articles } from '@/fake_articles.json'

export default {
  components: {
    MarkdownDisplay,
    ArticlePreviewAside,
    TagPill
  },

  computed: {
    // finds the current article
    currentArticle () {
      const id = this.$route.params.id

      function findArticle (id) {
        return articles.find(article => article.slug === id)
      }

      return articles[id] || findArticle(id)
    }
  }
}
</script>

